@import '../../stylemodules/main.scss';

.ziple-service-page {
    main.service-category {
        padding: 120px 0;

        @include responsive(TABLET) {
            padding: 110px 0;
        }

        @include responsive(MOBILE-LG) {
            padding: 100px 0;
        }

        @include responsive(MOBILE-MD) {
            padding: 80px 0;
        }

        .service-category-sub {
            font-size: $md-font-size-d;
            font-weight: 600;
            color: $sub-color;
            padding-bottom: 60px;

            @include responsive(TABLET) {
                font-size: $lg-font-size-m;
                padding-bottom: 55px;
            }

            @include responsive(MOBILE-LG) {
                font-size: $lg-font-size-m;
                padding-bottom: 45px;
            }

            @include responsive(MOBILE-MD) {
                font-size: $lg-font-size-m;
                padding-bottom: 40px;
            }
        }

        .service-category-title {
            font-size: $lg-font-size-d;
            font-weight: 600;
            color: $main-color-darnken;
            padding-bottom: 100px;

            @include responsive(TABLET) {
                font-size: $lg-font-size-t;
                padding-bottom: 70px;
            }

            @include responsive(MOBILE-LG) {
                font-size: $lg-font-size-m;
                padding-bottom: 60px;
            }

            @include responsive(MOBILE-MD) {
                font-size: $lg-font-size-m;
                padding-bottom: 50px;
            }
        }

        .service-category-list {
            width: 100%;
            display: flex;
            justify-content: space-around;
            flex-wrap: wrap;

            li {
                width: 33.3%;
                margin-top: 30px;

                @include responsive(TABLET) {
                    width: 50%;
                }

                @include responsive(MOBILE-LG) {
                    width: 50%;
                }

                @include responsive(MOBILE-MD) {
                    width: 100%;
                }

                img {
                    padding-bottom: 20px;
                }

                h6 {

                    padding-bottom: 20px;
                    font-size: $sm-font-size-d;
                    font-weight: 600;
                    color: #585858;

                }

                p {
                    color: $sub-color;
                    font-weight: 800;
                }
            }
        }
    }

    // 제작 및 납품 절차
    main.service-production {
        overflow:hidden;
        padding: 120px 0;
        background: #2962B8;

        @include responsive(TABLET) {
            padding: 110px 0;
        }

        @include responsive(MOBILE-LG) {
            padding: 100px 0;
        }

        @include responsive(MOBILE-MD) {
            padding: 80px 0;
        }

        h1.service-production-title {
            font-size: $lg-font-size-t;
            color: $main-color-lighten;
            font-weight: 600;
            padding-bottom: 30px;

            @include responsive(TABLET) {
                font-size: $md-font-size-t;
            }

            @include responsive(MOBILE-LG) {
                font-size: 1.2rem;
            }

            @include responsive(MOBILE-MD) {
                font-size: $lg-font-size-m;
            }
        }

        h3.service-production-sub {
            font-size: $sm-font-size-d;
            font-weight: 400;
            color: $main-color-lighten;
            padding-bottom: 60px;

            @include responsive(TABLET) {
                font-size: $sm-font-size-t;
                padding-bottom: 55px;
            }

            @include responsive(MOBILE-LG) {
                font-size: $sm-font-size-m;
                padding-bottom: 45px;
            }

            @include responsive(MOBILE-MD) {
                font-size: $sm-font-size-m;
                padding-bottom: 40px;
            }
        }

        p.service-production-bar-wrapper {
            width: 100%;
            height: 3px;
            background: #020066;
            filter: blur(0.5px);
            margin-bottom: 20px;
            position: relative;
            overflow: hidden;

            span.service-production-bar {
                position: absolute;
                background: $main-color-lighten;
                transition: all 1.2s;
                width: 0px;
                z-index: 300;
                top: 0;
                left: 0;
                height: 3px;
            }
        }
        div.service-production-progress-wrapper {
        
        ul.service-production-progress {
            
            display: flex;
            flex-wrap: nowrap;

            li.service-production-progress-title {
                width: 10%;
                font-weight: 700;
                color: $main-color-lighten;

                b {
                    font-size: 1rem;
                    line-height: 30px;
                }

                h4 {
                    font-size: 0.8rem;
                    line-height: 30px;
                }

                p {
                    text-align: left;
                    font-weight: 400;
                    font-size: 0.7rem;
                    padding-left: 23px;
                    line-height: 15px;
                }
            }
        }
    }
    }
}