@import '../../stylemodules/main.scss';

div.ziple-output-page {

    // 개발산출물 슬라이드쇼
    main.output-slide-wrapper {
        padding: 120px 0;

        @include responsive(TABLET) {
            padding: 110px 0;
        }

        @include responsive(MOBILE-LG) {
            padding: 100px 0;
        }

        @include responsive(MOBILE-MD) {
            padding: 80px 0;
        }

        h2.output-slide-sub {
            font-size: $md-font-size-d;
            font-weight: 600;
            color: $sub-color;
            padding-bottom: 33px;

            @include responsive(TABLET) {
                font-size: $lg-font-size-m;
                padding-bottom: 28px;
            }

            @include responsive(MOBILE-LG) {
                font-size: $lg-font-size-m;
                padding-bottom: 28px;
            }

            @include responsive(MOBILE-MD) {
                font-size: $lg-font-size-m;
                padding-bottom: 25px;
            }
        }

        h1.output-slide-title {
            font-size: $lg-font-size-d;
            font-weight: 700;
            color: $main-color-darnken;
            padding-bottom: 33px;

            @include responsive(TABLET) {
                font-size: $lg-font-size-t;
                padding-bottom: 28px;
            }

            @include responsive(MOBILE-LG) {
                font-size: $lg-font-size-m;
                padding-bottom: 25px;
            }

            @include responsive(MOBILE-MD) {
                font-size: $lg-font-size-m;
                padding-bottom: 25px;
            }
        }

        h6.output-slide-description {
            font-size: $sm-font-size-t;
            color: $sub-color;
            padding-bottom: 33px;

            @include responsive(TABLET) {
                font-size: $sm-font-size-t;
                padding-bottom: 28px;
            }

            @include responsive(MOBILE-LG) {
                font-size: $sm-font-size-m;
                padding-bottom: 25px;
            }

            @include responsive(MOBILE-MD) {
                font-size: $sm-font-size-m;
                padding-bottom: 25px;
            }
        }

        section.output-slide-wrapper {
            div.output-slide-inner {
                div {
                    h3 {
                        background: red;
                        width: 300px;
                        height: 300px;
                    }

                }
            }
        }

    }

    main.output-category-wrapper {
        background: #F8F8F8;
        padding: 120px 0;

        @include responsive(TABLET) {
            padding: 110px 0;
        }

        @include responsive(MOBILE-LG) {
            padding: 100px 0;
        }

        @include responsive(MOBILE-MD) {
            padding: 80px 0;
        }

        h2.output-category-sub {
            font-size: $md-font-size-d;
            font-weight: 600;
            color: $sub-color;
            padding-bottom: 33px;

            @include responsive(TABLET) {
                font-size: $lg-font-size-m;
                padding-bottom: 28px;
            }

            @include responsive(MOBILE-LG) {
                font-size: $lg-font-size-m;
                padding-bottom: 28px;
            }

            @include responsive(MOBILE-MD) {
                font-size: $lg-font-size-m;
                padding-bottom: 25px;
            }
        }

        h1.output-category-title {
            font-size: $lg-font-size-d;
            font-weight: 700;
            color: $main-color-darnken;
            padding-bottom: 33px;

            @include responsive(TABLET) {
                font-size: $lg-font-size-t;
                padding-bottom: 28px;
            }

            @include responsive(MOBILE-LG) {
                font-size: $lg-font-size-m;
                padding-bottom: 25px;
            }

            @include responsive(MOBILE-MD) {
                font-size: $lg-font-size-m;
                padding-bottom: 25px;
            }
        }
    }

    .output-slide02-wrapper {
        padding: 120px 0;

        @include responsive(TABLET) {
            padding: 110px 0;
        }

        @include responsive(MOBILE-LG) {
            padding: 100px 0;
        }

        @include responsive(MOBILE-MD) {
            padding: 80px 0;
        }

        .output-slide02-title {
            font-size: $md-font-size-d;
            font-weight: 700;
            color: $main-color-darnken;
            padding-bottom: 63px;

            @include responsive(TABLET) {
                font-size: $md-font-size-t;
                padding-bottom: 44px;
            }

            @include responsive(MOBILE-LG) {
                font-size: $md-font-size-m;
                padding-bottom: 33px;
            }

            @include responsive(MOBILE-MD) {
                font-size: $lg-font-size-m;
                padding-bottom: 22px;
            }
        }

        div.section09-slide-container {
            .section09-slide-item {
                height: 300px;

                div {
                    display: flex;
                    flex-direction: column;
                    align-items: center;

                    img {
                        width: 130px;
                        height: 130px;
                        padding-bottom:10px;
                    }

                    h3 {
                        font-size:$lg-list-size;
                        padding-bottom:10px;
                    }   

                    p {
                        text-align: left;
                        font-size:$md-list-size;
                        color: gray;
                        line-height:15px;
                    }
                }

            }
        }
    }
}