@import '../../stylemodules/main.scss';

.ziple-request {
    .request-client {
        padding: 120px 0;

        @include responsive(TABLET) {
            padding: 110px 0;
        }

        @include responsive(MOBILE-LG) {
            padding: 100px 0;
        }

        @include responsive(MOBILE-MD) {
            padding: 80px 0;
        }

        h2.request-client-sub {
            font-size: $md-font-size-d;
            font-weight: 600;
            color: $sub-color;
            padding-bottom: 33px;

            @include responsive(TABLET) {
                font-size: $lg-font-size-m;
                padding-bottom: 28px;
            }

            @include responsive(MOBILE-LG) {
                font-size: $lg-font-size-m;
                padding-bottom: 28px;
            }

            @include responsive(MOBILE-MD) {
                font-size: $lg-font-size-m;
                padding-bottom: 25px;
            }
        }

        h1.request-client-title {
            font-size: $lg-font-size-d;
            font-weight: 700;
            color: $main-color-darnken;
            padding-bottom: 33px;

            @include responsive(TABLET) {
                font-size: $lg-font-size-t;
                padding-bottom: 28px;
            }

            @include responsive(MOBILE-LG) {
                font-size: $lg-font-size-m;
                padding-bottom: 25px;
            }

            @include responsive(MOBILE-MD) {
                font-size: $lg-font-size-m;
                padding-bottom: 25px;
            }
        }

        .request-client-list {
            display: flex;
            flex-wrap: wrap;

            li {
                width: 25%;

                img {
                    width: 100%;
                    height: 200px;
                }
            }
        }
    }

    .request-banner {
        padding: 120px 0;
        background: #7097FB;

        @include responsive(TABLET) {
            padding: 110px 0;
        }

        @include responsive(MOBILE-LG) {
            padding: 100px 0;
        }

        @include responsive(MOBILE-MD) {
            padding: 80px 0;
        }

        .request-banner-content {
            border-bottom: 1px solid lightgray;
            padding-bottom: 33px;

            .request-banner-title {
                font-size: 60px;
                color: $main-color-lighten;
                font-weight: 700;
                padding-bottom: 33px;
                letter-spacing: 1px;
            }

            .request-banner-description {
                color: $main-color-lighten;
                font-size: 16px;
                font-weight: 300;

                b {
                    font-weight: bold;
                }
            }
        }
    }

    .request-banner-button {
        display: block;
        padding: 12px 24px;
        background: #FAFAFA;
        width: 160px;
        margin: 0 auto;
        cursor: pointer;
        margin-top:33px;
    }
}